@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&family=Roboto+Mono:ital,wght@1,300&family=Rubik+Iso&display=swap');

.ExamStart{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 84vh;
    justify-content: center;
    width: 100%;
    background-image: radial-gradient( circle 1023px at 3.1% 6.9%,  rgba(255,255,255,1) 0%, rgb(215, 154, 171) 74.9% );
}
.ExamStart .chosenBox{
    margin-bottom: 9px;
    font-family: 'Roboto Mono', monospace;
    color: blueviolet;
    font-size: 20px;
    font-weight: 900;
}
.ExamStart .chosenBox select{
  padding: 4px;
  outline: none;
  width: 200px;
  border-radius: 6px;
  box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
  border: none;
  color: #1e5c30;
  font-family: 'Roboto Mono', monospace;
  font-weight: 900;
}
.ExamStart .chosenBox select option{
  padding: 4px;
  outline: none;
  width: 200px;
  border-radius: 4px;
  box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
  border: none;
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
 
}
.ExamStart .title{
    margin: 10px;
    width: 50%;
    display: flex;
   justify-content: center;
    align-items: center;
    height: 40px;
    font-size: 25px;
    
}
.ExamStart .title span{
    color: red;
    font-family: 'Rubik Iso', cursive;
}

.rules-list ul{
    margin: 10px;
    list-style-type: none;
    color: crimson;
    font-weight: 900;
    font-size: 15px;
    font-family: 'Roboto Mono', monospace
}
.rules-list ul li{
    margin: 10px;
    
}

.regulation{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    color: rgb(177, 16, 16);
    font-size: 15px;
    font-family: 'Roboto Mono', monospace;
    font-weight: 900;
}
strong{
    color: red;
}
.ExamStart .Button {
   
   width: 90%;
   display: flex;
   justify-content: right;
   align-items: center;
   
   margin-left: 10px;
   font-family: 'Rubik Iso', cursive;
}

 .ExamStart .Button button{
    height: 40px;
    padding: 0 20px;
    font-weight:400;
    letter-spacing: 2px;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}
.Button .Start{
    background: #14dc4c;
}

@media  screen and (max-width:532px) {
    .ExamStart{
        width: 122vw;
        height: 100vh;
    }
    .ExamStart .chosenBox{
        margin-top: 30px;
        font-size: 14px; 
           
    }
    .ExamStart .title span{
        font-size: 17px;
    }
    .rules-list ul, .regulation{
        font-size: 12px;
    }
}