@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&family=MuseoModerno:wght@100&family=Overpass:wght@100&family=Rokkitt:wght@100&display=swap');


.ExamCreation{
    display: flex;
    flex-direction: column;
    background-image: linear-gradient( 58.2deg,  rgba(40,91,212,0.73) -3%, rgba(171,53,163,0.45) 49.3%, rgba(255,204,112,0.37) 97.7% );
}
.ExamCreation .dash-header{

    display: flex;
    justify-content: space-between;
    margin: 2px;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;

    box-shadow: 0 0 14px rgba(0, 0, 0, 0.5); 
}
.ExamCreation .dash-header .dash-welcome{
    font-size: 29px;
    letter-spacing: 2px;
    font-family: 'Josefin Sans', sans-serif;
    color: #ec2525;
}
.ExamCreation .dash-header .nav_icon{
    font-size: 10px;
    letter-spacing: 2px;
    font-family: 'MuseoModerno', cursive;
    color: rgb(209, 57, 57);
    display: flex;
    flex-direction: column; 
    align-items: center;  
    font-weight: 900;
}
.dash-header .nav_icon img{
    mix-blend-mode: darken;
    width: 40px;
    margin-bottom: -5px;
   
}
.ExamCreation .createExamBody{
    display: flex;
    justify-content: center; 
   
    font-family: 'Overpass', sans-serif;
    font-weight: 800;
}
.ExamCreation form{
    display: flex;
    width: 70%;
    flex-direction: column;

    /* justify-content: center; */
    
}
.ExamCreation .createExamBody .ExamDetails{
    display: flex;
    flex-direction: column;
  padding: 5px;
    
    margin: 20px; 
    height: fit-content; 
    width: 50%;
    margin-left: 60px;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.5); 
}
.ExamCreation .createExamBody .ExamDetails .input-element {
    display: flex;
    margin: 6px;
    justify-content: space-between;
}
.ExamCreation .createExamBody .ExamDetails .input-element label{
    margin-right: 10px;
   
}
.ExamCreation .createExamBody .ExamDetails .input-element input{
    margin-right: 10px;
    border: 2px solid grey;
    border-radius: 5px;
    padding: 3px;
   
}
.ExamCreation .UploadSection{
    height: 200px;
    width: 300px;
    border: 2px solid rgba(187, 32, 235, 0.61);
    margin-left: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ExamCreation .UploadSection .UploadFile{
    height: 200px;
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); 
}
.ExamCreation .UploadSection .UploadFile .Dropzone{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 180px;
    width: 200px;
    border: 2px dashed rgb(172, 13, 187);
    background-image: radial-gradient( circle 588px at 31.7% 40.2%,  rgba(225,200,239,1) 21.4%, rgba(163,225,233,1) 57.1% );
    border-radius: 20%;
  
}
.ExamCreation .UploadSection .UploadFile .Dropzone img{
    height: 50px;
    width: 50px;
   
}
.ExamCreation .UploadSection .UploadFile .Dropzone p{
    margin-bottom: 20px;
    font-size: 10px;
}

.ExamCreation .UploadSection button{
    display: inline-block;
    outline: 0;
    border: 0;
    cursor: pointer;
    background: #FCFCFD;
    box-shadow: 0px 2px 4px rgb(45 35 66 / 40%), 0px 7px 13px -3px rgb(45 35 66 / 30%), inset 0px -3px 0px #d6d6e7;
    height: 35px;
    padding: 0 20px;
    font-size: 13px;
    border-radius: 6px;
    color: #36395a;    
}

.ExamCreation .submit-button{
    font-family: 'Rokkitt', serif;
    display: flex;
    align-items: center;
    justify-content: right;
    margin: 20px;
}





.submit-button input[type = submit]{
  background-color: #c2fbd7;
  border-radius: 20px;
  box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset,rgba(44, 187, 99, .15) 0 1px 2px,rgba(44, 187, 99, .15) 0 2px 4px,rgba(44, 187, 99, .15) 0 4px 8px,rgba(44, 187, 99, .15) 0 8px 16px,rgba(44, 187, 99, .15) 0 16px 32px;
  color: green;
  cursor: pointer;
  display: inline-block;
  font-family: 'Rokkitt', serif;
  font-weight: 900;
  padding: 7px 20px;
  text-align: center;
  text-decoration: none;
  transition: all 250ms;
  border: 0;
  font-size: 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.submit-button input[type = submit]:hover {
  box-shadow: rgba(44,187,99,.35) 0 -25px 18px -14px inset,rgba(44,187,99,.25) 0 1px 2px,rgba(44,187,99,.25) 0 2px 4px,rgba(44,187,99,.25) 0 4px 8px,rgba(44,187,99,.25) 0 8px 16px,rgba(44,187,99,.25) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}



.ExamCreation .Guideline{
   display: flex;
 width: 400px;
 height: 300px;
 align-items: center;
 justify-content: center;
color: #9b0707;
flex-direction: column;
   
}
.ExamCreation .Guideline img{
    text-align: center;
   height: 30px;
   width: 30px;
  mix-blend-mode: darken;
   margin-bottom: 10px;
}
.ExamCreation .Guideline ul{
  list-style-type: disc ;
}

.ExamCreation .Guideline li{
    margin: 4px;
    font-family: 'Rokkitt', serif;
}
.ExamCreation .Guideline a{
    margin-top: 10px;
    font-family: 'Rokkitt', serif;
    font-weight: 900;
    text-decoration: none;
    
    display: inline-block;
    outline: none;
    cursor: pointer;
    font-weight: 600;
    border-radius: 3px;
    padding: 12px 24px;
    border: 0;
    color: #3a4149;
    background: #e7ebee;
    line-height: 1.15;
    font-size: 16px;
}
.ExamCreation .Guideline a:hover{
        transition: all .1s ease;
        box-shadow: 0 0 0 0 #fff, 0 0 0 3px #1de9b6;
        color: #1de9b6;
}

@media  screen and (max-width:1024px) {
   .ExamCreation .createExamBody {
    flex-direction: column;
    
   }
   .ExamCreation .UploadSection{
    height: 200px;
    width: 250px;
    margin-left: 10px;
}
.ExamCreation .UploadSection .UploadFile{
    height: 200px;
    width: 250px;
}
.ExamCreation form{
    width: 100%; 
}
.ExamCreation .createExamBody .ExamDetails{  
    width: 90%;
    margin-left: 10px;
}

.ExamCreation .createExamBody .ExamDetails .input-element label{
    margin-right: 10px;
   
}
}

@media  screen and (max-width:600px) {
    .ExamCreation{
        width: 120vw;
        height: 100%;
    }
    
}