@import url('https://fonts.googleapis.com/css2?family=Antic+Slab&family=Changa:wght@300&family=Khand:wght@300&display=swap');

*{
    margin: 0;
}
.navbar{
   width: 100%;
}

.Heading{
    display: flex;
    justify-content: space-between;
    height: 100px;
    
    /* border: 2px solid black; */
    width: 100%;
    margin: 0;
    padding: 0;
    background-image: linear-gradient(to right, #74ebd5 0%, #9face6 100%);
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    pointer-events: none;
    user-select: none;
    
}
.Heading .logo{
    height: 80px;
    width: 100px;

    
    /* border: 2px solid red; */

}
.Heading .logo img{
    height: 80px;
    width: 100px;


}

.Heading .head_right{
    display: inline-block;
    color: white;
    width: 341px;
    font-family: 'Khand', sans-serif;
    font-size: 28px;
    text-transform: uppercase;
    margin-right:20px ;
    font-weight: 900;
    letter-spacing: 2px;
    text-shadow: 3px 3px 5px rgb(29, 19, 156);
    overflow: hidden;
    white-space: nowrap;  
}

body::-webkit-scrollbar {
    width: 14px;               /* width of the entire scrollbar */
  }
  
  body::-webkit-scrollbar-track {
    background: orange;        /* color of the tracking area */
  }
  
  body::-webkit-scrollbar-thumb {
    background-color: blue;    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    border: 3px solid orange;  /* creates padding around scroll thumb */
  }


  /* @keyframes typing {
    from {width: 0;
    }
    
  } */
 
  .head_right{
    
    position: absolute;
    margin-left:100px ;
 
  }
  
  .head_right:nth-child(1) {
    color: transparent;
    
  }
  
  .head_right:nth-child(2) {
    color: #d4eaf5;
    animation: animate 4s ease-in-out infinite;
  }
  
  @keyframes animate {
    0%,
    100% {
      clip-path: polygon(
        0% 45%,
        16% 44%,
        33% 50%,
        54% 60%,
        70% 61%,
        84% 59%,
        100% 52%,
        100% 100%,
        0% 100%
      );
    }
  
    50% {
      clip-path: polygon(
        0% 60%,
        15% 65%,
        34% 66%,
        51% 62%,
        67% 50%,
        84% 45%,
        100% 46%,
        100% 100%,
        0% 100%
      );
    }
  }
  


  @media  screen and (max-width:1024px) {
    
}

@media  screen and (max-width:600px) {
  .navbar{
    width: 122vw;
 }
 
  
  .Heading .logo{
    height: 60px;
    width: 70px;

}
.Heading .logo img{
    height: 60px;
    width: 70px;
}
.Heading .head_right{
  font-size: 22px;
}

}