@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Cantarell&family=Ubuntu+Condensed&display=swap');


.Exam-Data{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.Exam-Data .title{
    margin-top: 15px;
   font-size: 40px;
   text-transform: uppercase;
   font-family: 'Bree Serif', serif;
   letter-spacing: 9px;
   word-spacing: 5px;
   color: blueviolet;
}
.Exam-Data table{
    margin-top: 50px;
    padding: 5px;
    width: 90%;
    height: fit-content;
    border: 4px solid grey;
    text-align: center;
}
.Exam-Data table thead{
    background-color: blueviolet;
    color: white;
    margin: 10px;
}
.Exam-Data table thead tr th{
    font-size: 22px;
    padding: 10px;
    margin-bottom: 100px;
    font-family: 'Cantarell', sans-serif;
    letter-spacing: 1px;
}

.Exam-Data table td{
    font-size: 20px;
    font-family: 'Ubuntu Condensed', sans-serif;
}

.Exam-Data .delete{
    border: none;
    background: linear-gradient(red,rgb(231, 66, 66));
    color: whitesmoke;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);

}

@media  screen and (max-width:1024px) {
    .Exam-Data table thead tr th{
        font-size: 15px;
        padding: 7px;
        margin-bottom: 10px;
    }
    .Exam-Data table td{
        font-size: 16px;
        
    }
    
    .Exam-Data .delete{
        border: none;
        background: linear-gradient(red,rgb(231, 66, 66));
        color: whitesmoke;
        padding: 5px;
        border-radius: 10px;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    
    }
}

@media  screen and (max-width:600px) {
    .Exam-Data table thead tr th{
        font-size: 10px;
        padding: 1px;
        margin-bottom: 100px;
    }
    .Exam-Data table td{
        font-size: 10px;
        font-family: 'Ubuntu Condensed', sans-serif;
    }
    
    .Exam-Data .delete{
        padding: 5px;
        border-radius: 10px;
        
    
    }
    
}