@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Cantarell&family=Ubuntu+Condensed&display=swap');


.Student-data{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.Student-data .title{
    margin-top: 15px;
   font-size: 40px;
   text-transform: uppercase;
   font-family: 'Bree Serif', serif;
   letter-spacing: 9px;
   word-spacing: 5px;
   color: blueviolet;
}
.Student-data table{
    margin-top: 50px;
    padding: 5px;
    width: 90%;
    height: fit-content;
    border: 4px solid grey;
    text-align: center;
}
.Student-data table thead{
    background-color: blueviolet;
    color: white;
    margin: 10px;
}
.Student-data table thead tr th{
    font-size: 22px;
    padding: 10px;
    margin-bottom: 100px;
    font-family: 'Cantarell', sans-serif;
    letter-spacing: 1px;
}

.Student-data table td{
    font-size: 20px;
    font-family: 'Ubuntu Condensed', sans-serif;
}
.Student-data table img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
    border: 2px solid black;
}
.Student-data .username{
    color:crimson;
}
.Student-data p{
    color:green;
}
.Student-data .permission{
    border: none;
    background: linear-gradient(rgb(53, 218, 20),rgb(15, 150, 48));
    color: whitesmoke;
    padding: 10px;
    border-radius: 10px;

}
.Student-data .delete{
    border: none;
    background: linear-gradient(red,rgb(231, 66, 66));
    color: whitesmoke;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);

}

@media  screen and (max-width:1024px) {
   
    
    .Student-data table thead tr th{
        font-size: 12px;
        padding: 5px;
    }
    
    .Student-data table td{
        font-size: 14px;
    }
    .Student-data table img{
        height: 50px;
        width: 50px; 
        border: 1px solid black;
    }
    .Student-data .permission{
        padding: 5px;
        border-radius: 5px;
        font-size: 10px;
    }
    .Student-data .delete{
        border: none;
        background: linear-gradient(red,rgb(231, 66, 66));
        color: whitesmoke;
        padding: 8px;
        border-radius: 5px;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    
    }
    
}

@media  screen and (max-width:600px) {
    .Student-data{
        width: 120vw;
    }
    .Student-data .title{
        font-size: 20px;
    }
    .Student-data table thead tr th{
        font-size: 10px;
        padding: 2px;
    }
    
    .Student-data table td{
        font-size: 10px;
    }
    .Student-data table img{
        height: 30px;
        width: 30px; 
        border: 1px solid black;
    }

    
}