
@import url('https://fonts.googleapis.com/css2?family=Belanosima&family=Passion+One&family=Rubik+Wet+Paint&display=swap');


.Home{
    display: flex;
    flex-direction: column;
    background-color: #8BC6EC;
    background-image: radial-gradient( circle farthest-corner at 92.3% 71.5%,  rgba(83,138,214,1) 0%, rgba(134,231,214,1) 90% );
height: 100vh;

}
.Home .home-header{

    display: flex;
    justify-content: space-between;
    margin: 2px;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.5); 
}
.Home .home-header .st-welcome{
    font-size: 29px;
    letter-spacing: 2px;
    font-family: 'Rubik Wet Paint', cursive;
    color: darkblue;
}
.Home .home-header .log-out{
    font-size: 20px;
    letter-spacing: 2px;
    font-family: 'Passion One', cursive;
    color: rgb(209, 57, 57);
    display: flex;
    flex-direction: column; 
    align-items: center;  
}
.Home .home-header .log-out img{
    mix-blend-mode: darken;
    width: 40px;
    margin-bottom: -5px;
   
}

.Home .st-area{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.Home .st-area .funtion-profile{
    display: flex;
    flex-direction: row;
}

.Home .st-area .st-function .fn-box{
    border: 2px outset rgb(20, 178, 99);
    padding: 10px;
    height: 160px;
    width: 120px;
    margin: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 2px 0 14px rgba(66, 225, 98, 0.945); 
    border-radius: 14px;
    font-family: 'Belanosima', sans-serif;
    text-align: center;
    text-transform: capitalize;
    font-size: 20px;
    color: rgb(222, 226, 230);
}
.Home .st-area .st-function .fn-box:hover{
    border: 2px solid rgb(5, 150, 53);
    padding: 7px;
    color: #057616;
    box-shadow: 0 0 14px rgba(2, 248, 105, 0.945); 
}
.Home .st-area .st-function img{
    /* mix-blend-mode: multiply; */
    height: 125px;
    width: 100px; 
}
.Home .st-profile{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
    border-radius: 5px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: inset -1px 0 10px rgba(0, 0, 0, 0.5);
    background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgb(13, 194, 94) 0%, rgba(12,116,117,1) 90% ); 
    
}
.Home .st-profile img{
    height: 60px;
    width: 70px;
}

.Home .st-profile .profile_pic{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.5);  
    margin-top: 10px; 
    margin-bottom: 10px;
}
.Home .st-details{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    font-size: 25px;
    font-family: 'Belanosima', sans-serif;
}
.Home .st-details p{
    padding-bottom: 10px;
    font-size: 25px;
}
.Home span{
    color: white;
    text-shadow: 3px 3px 5px rgb(58, 48, 185);
}

@media  screen and (max-width:1024px) {
    
    .Home .home-header .st-welcome{
        font-size: 20px;
    }
}

@media  screen and (max-width:640px) {
   .Home{
    height: 120vh;
    width: 122vw;

   }
    .Home .st-area{
        flex-direction: column;
        align-items: center;
    }
    .Home .st-profile{
        height: 200px;
        margin: 15px; 
    }
    
    .Home .st-details p{
        padding-bottom: 5px;
        font-size: 15px;
    }
    .Home .st-profile img{
        height: 40px;
        width: 50px;
    }
    
    .Home .st-profile .profile_pic{
        height: 50px;
        width: 50px;
    }
}