
@import url('https://fonts.googleapis.com/css2?family=Aref+Ruqaa+Ink&family=Edu+NSW+ACT+Foundation&family=Hubballi&family=New+Tegomin&display=swap');

/* :root{
  --progress-value : 0,
} */
.ExamTime{
    display: flex;
    flex-direction: column;
    background-image: linear-gradient( 109.6deg,  rgba(254,253,205,1) 11.2%, rgba(163,230,255,1) 91.1% );
}
.ExamTime header{
    display: flex;
    font-size: 14px;
    align-items: center;
    height: 30px;
    justify-content: right;
    padding: 10px;
    margin-right:18px ;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    font-family: 'New Tegomin', serif;
    font-weight: 900;
    user-select: none;
}
.ExamTime header p{
   margin: 10px;
}
.ExamTime header p span{
  color: #1e03b3;
  font-size: 20px;
}
.ExamTime header p:last-child span{
  color: #03b303;
  font-size: 30px;
}

.examArea{
    height: 75.5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.examBox{

    height: 500px ;
    width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
}
.examBox .ExamBoxHeader {
   
    width:95% ;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
    margin-left: 10px;
    user-select: none;
    box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
    font-family: 'Hubballi', sans-serif;
    
    font-weight: 900;
}
.examBox .ExamBoxHeader .Ques_no {
   
    width: 200px;
    display: flex;
    word-spacing: 4px;
    align-items: center;  
    font-size: 20px; 
}
.examBox .ExamBoxHeader .Ques_no span {
  
     margin-left:10px ;
     margin-right:10px ;
    display: flex;
    word-spacing: 10px;
    align-items: center;
    font-size: 25px;
}
.examBox .ExamBoxHeader .Ques_no span:nth-child(1) {
  
    color: rgb(19, 143, 185);
}
.examBox .ExamBoxHeader .Ques_no span:nth-child(2) {
  
   color: lightslategray;
}

.examBox .ExamBoxHeader .Timebox {
    border: 2px solid grey;
    width:150px ;
    padding: 9px;
    display: flex;
    margin: 6px;
    justify-content: center;
    align-items: center;
    background-color: #cce5ff;
    border: 1px solid #b8daff;
    border-radius: 8px;
    font-size: 25px;
}
.examBox .ExamBoxHeader .Timebox span {
    font-weight: 500;
    font-size: 18px;
    user-select: none;
    background: black;
    width: 45px;
    color: #fff;
    text-align: center;
    line-height: 30px;
    border-radius: 6px;
    margin-left: 7px;
    box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.9); 
    animation: countdown 1s  infinite;
    transition: font-size 0.1s ease;
}

.Question{
    height: 70px;
    margin-left: 20px;
    padding: 5px;
    user-select: none;
    font-family: 'Hubballi', sans-serif;
    font-size: 20px;
  
}
.answerBox{
    display: flex;
    flex-direction: column;
   height: 200px;
   width: 90%;
   user-select: none;
   font-family: 'Hubballi', sans-serif;
   font-size: 18px;
    font-weight: 700;
}
.answerBox span{
    display: flex;
    flex-direction: column;
   margin: 10px;
   margin-left: 20px;
   padding: 5px;
   background: aliceblue;
   border: 1px solid #84c5fe;
   border-radius: 10px;
   cursor: pointer;
   transition: all 0.5s ease;
    
}

   .answerBox span:hover{
    background: rgb(140, 216, 146);
    border: 1px solid #1e03b3;
   color: white;
   }
   .answerBox span:focus{
    background: rgb(36, 236, 53);
    border: 1px solid #1e03b3;
   font-size: 20px; 
   color: white;
   font-size: 20px;
   }
 .Buttons{
    margin-top: 10px;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: right;
    margin-bottom: 10px;
   margin-left: 30px;
   font-family: 'Hubballi', sans-serif;
   user-select: none;
 }

   .Buttons button{
    height: 40px;
    padding: 0 30px;
    letter-spacing: 2px;
    font-size: 20px;
    border: none;
    outline: none;
    color: #ffffff;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    font-weight:700 ;
  }
   .Buttons button:hover{
    
    font-size: 22px;
    font-weight:800 ;
    transition: all ease 0.1s;
  }
   .Buttons button:hover{
    
    font-size: 22px;
    font-weight:800 ;
    transition: all ease 0.1s;
  }
  .Buttons #Next{
    background-color: rgb(8, 168, 43);
  }
  .Buttons #Finish{
    background-color: rgb(139, 12, 197);
  }
  .Buttons #logout{
    background-color: rgb(243, 42, 42);
  }

 
  @keyframes countdown {
    0%{
        font-size: 10px;
    }
    100%{
        font-size: 20px;
    }
    
  }


  .ScoreView{
    display : flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
    width: 90%;
    height: 200px;
    user-select: none;
  }
  .ScoreView .progress{
   width: 150px;
   height: 150px;
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-left: 30px;
   box-shadow: inset 3px 3px 8px rgba(0, 0, 0, 0.2);
  }
  

  .progress::before {
    content: attr(data) '%';
    animation: fillProgress 2s ease-in-out;
}

@keyframes fillProgress {
    from {
        content: "0%";
    }
    to {
        content: attr(data) '%';
    }
}
  .circle {
    background: 
      radial-gradient(closest-side, white 79%, transparent 80% 100%),
      conic-gradient(rgb(35, 218, 66) calc(var(--progress-value) * 1%), rgb(180, 212, 216) 0);
   
    font-family: 'Hubballi', sans-serif;
    font-weight: 900;
    font-size: 30px;
    
    color: red;
  }
  
  /* .circle::before {
    
  } */
  progress {
    visibility: hidden;
    width: 0;
    height: 0;
  }
  .ScoreView .Score_text{
    font-family: 'Hubballi', sans-serif;
    font-weight: 900;
    font-size: 25px;
    margin-left: 15px;
    text-wrap: wrap;
    margin-right: 10px;
  }
  .ScoreView .Score_text span{
    color: #1e03b3;
    font-size: 30px;
    font-weight: 900;
  }


  @media  screen and (max-width:1024px) {
    .ExamTime header p{
     font-size: 11px;
   }
   .ExamTime header p span{
     font-size: 14px;
   }
   .ExamTime header p:last-child span{
     font-size: 20px;
   }
    
}

@media  screen and (max-width:600px) {
  .ExamTime{
    height: 100vh;
    width: 122vw;
  }
  .ExamTime header p {
    margin: 5px;
}
    .examBox{
      width: 350px;
    }
  .examBox .ExamBoxHeader {
     width:330px;
}
.examBox .ExamBoxHeader .Ques_no {
    width: 150px;
    word-spacing: 2px;
    font-size: 17px; 
}

.examBox .ExamBoxHeader .Timebox {
    width:100px ;
    font-size: 15px;
}

.Question{
  
    font-size: 17px;
    height: 100px;
    display: flex;
    align-items: center;
}
.answerBox{
   font-size: 15px; 
}
  .Buttons #Next{
    background-color: rgb(8, 168, 43);
    margin-top: 10px;
  }
  .Buttons #Finish{
    margin-top: 10px;
    background-color: rgb(139, 12, 197);
  }

  .ScoreView .progress{
   width: 100px;
   height: 100px;
  }
  .ScoreView .circle{
   font-size: 20px;
    width: 155px;
    height: 100px;
  }
}