.StartPage{
    
    width: 100%;
    height : 100vh;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    background-color: #85FFBD;
    background-image: linear-gradient(109deg, #85FFBD 0%, #FFFB7D 100%);
    background-size: cover;
    
}

.container{
    /* border: 2px solid green; */
    margin-top: 20px;
    height: 100%;
    display: flex;
    flex-direction: column;
   align-items: center;
}
.student_login_button , .admin_login_button{
 /* border: 1px solid red; */
 padding: 5px;
 margin-top: 20px;
 align-items: center;

}

.st_box,.ad_box{
    /* border: 2px solid red; */
    width: 330px;
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    padding: 5px;
    cursor: pointer;
    border-radius: 5%;
}
.st_box:hover,.ad_box:hover{
   
    box-shadow: 0 0 10px rgba(59, 214, 67, 0.993);
   
 
}
.st_box{
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
    
}
.ad_box{
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);}
.st_box img , .ad_box img{
    height: 50px;
    width: 40px;
    margin-right: 30px;
   
    border-radius: 20%;
}

.helpdesk{
    margin-top: 60px;
   
    height:150px;
    width: 350px;
    background: linear-gradient(grey,white); 
}
.helpdesk .helpdesk_head{
    height: 50px;
    display: flex;
    align-items: center;
    color: rgb(223, 24, 9);
    font-family: 'Changa', sans-serif;
    font-size: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    margin-bottom: 20px;
    
}
.helpdesk .helpdesk_head img{
    height: 60px;
   
}

.helpdesk .helpline{
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(165, 83, 72);
  
}
.helpdesk .helpline p{
    margin: 10px;
    
}


.text .highlight{
    font-family: 'Antic Slab', serif;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
}
.text .normal{
    font-family: 'Changa', sans-serif;
    font-size: 15px;
}


@media  screen and (max-width:1024px) {
    .StartPage{
        height: 100vh;
        
    }
}

@media  screen and (max-width:600px) {
    .StartPage{
        height: 100vh;
        width: 122vw;
    }
    .container{
        width: 384px;
    }
    
}