@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Cantarell&family=Ubuntu+Condensed&display=swap');


.ResultView{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.ResultView .title{
    margin-top: 15px;
   font-size: 40px;
   text-transform: uppercase;
   font-family: 'Bree Serif', serif;
   letter-spacing: 9px;
   word-spacing: 5px;
   color: rgb(9, 233, 110);
}
.ResultView table{
    margin-top: 50px;
    padding: 5px;
    width: 90%;
    height: fit-content;
    border: 4px solid grey;
    text-align: center;
}
.ResultView table thead{
    background-color: rgb(9, 233, 110);
    color: white;
    margin: 10px;
}
.ResultView table thead tr th{
    font-size: 22px;
    padding: 10px;
    margin-bottom: 100px;
    font-family: 'Cantarell', sans-serif;
    letter-spacing: 1px;
}

.ResultView table td{
    font-size: 20px;
    font-family: 'Ubuntu Condensed', sans-serif;
}


@media  screen and (max-width:1024px) {
    .ResultView .title{
        font-size: 25px;
    }
    .ResultView table{
       margin-left: 18px;
    }
    
    .ResultView table thead tr th{
        font-size: 15px;
        padding: 5px;
        
    }
    
    .ResultView table td{
        font-size: 17px;
    }
}

@media  screen and (max-width:600px) {
   
    .ResultView .title{
        font-size: 20px;
    }
    .ResultView table td{
        font-size: 15px;
        
    }
}