@import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Rajdhani:wght@300&display=swap');

.login{
    width: 100%;
    height: 100vh;
    position: relative;
}
.box{
    
    width: 400px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 30px;
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    
}
.box img{
    height: 60px;
    width: 50px;
    position: relative;
    left: 40%;
    pointer-events: none;
    user-select: none;
}
.box h1{
    margin-bottom: 30px;
    text-align: center;
    color: white;
    text-transform: uppercase;
    font-family: 'Lilita One', cursive;
    letter-spacing: 2px;
    user-select: none;
    
}
.box .inputBox{
    position: relative;
   
    font-family: 'Rajdhani', sans-serif;
}
.box .inputBox input{
 width: 100%;
 padding: 10px;
 font-size: 16px;
 color: white;
 letter-spacing: 1px;
 margin-bottom: 30px;
 border: none;
 border-bottom: 1px solid white;
 background: transparent;
 outline: none;
caret-color: aqua;
}
.box .inputBox label{
    position: absolute;
    top: 0;
    left: 0;
    letter-spacing: 1px;
    padding: 10px 0;
    font-size: 16px;
    color: white;
    transition: 0.5s;
    user-select: none;
    z-index: -99;
}


.box .inputBox input:focus ~ label,
.box .inputBox input:valid ~ label{
    top: -20px;
    left: 0;
    color: #220568;
    font-size: 12px;

}
.box input[type = 'submit']{
    font-family: 'Rajdhani', sans-serif;
    font-weight: 900; 
    letter-spacing: 4px;
    position: relative;
    background: transparent;
    border: none;
    outline: none;
    padding: 8px 16px;
    color: white;
    border-radius: 5px;
    background: #03a9f4;
    font-size: 18px;
    left: 70%;
    user-select: none;

}
.no_account{
    color: rgb(42, 43, 41);
    font-family: 'Rajdhani', sans-serif; 
    user-select: none;
}
.no_account .link{
   text-decoration: none;
   color: rgb(5, 153, 37);
   font-weight: 700;
}

.login .hideShow{
    position: absolute;
    left: 80%;
    bottom: 45%;
}
.login .hideShow img{
    height: 25px;
    width: 20px;
    opacity: 0.7;
   
}



@media  screen and (max-width:1024px) {
    .box{
        width: 300px;
        top: 35%;
        left: 50%;
    }
    .box img{
        height: 50px;
        width: 40px;
        margin: 0 auto;
        
    }
    .box h1{
        font-size: 20px;
    }
}

@media  screen and (max-width:600px) {
   .box{
    left: 185px;
    top: 245px;
   }
    
}