@import url('https://fonts.googleapis.com/css2?family=Belanosima&family=Passion+One&family=Rubik+Wet+Paint&display=swap');


.DashBoard{
    display: flex;
    flex-direction: column;
    
   

}
.DashBoard .dash-header{

    display: flex;
    justify-content: space-between;
    margin: 2px;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.5); 
}
.DashBoard .dash-header .dash-welcome{
    font-size: 29px;
    letter-spacing: 2px;
    font-family: 'Rubik Wet Paint', cursive;
    color: #f13333;
}
.DashBoard .dash-header .nav_icon{
    font-size: 20px;
    letter-spacing: 2px;
    font-family: 'Passion One', cursive;
    color: rgb(209, 57, 57);
    display: flex;
    flex-direction: column; 
    align-items: center;  
}
.dash-header .nav_icon img{
    mix-blend-mode: darken;
    width: 40px;
    margin-bottom: -5px;
   
}

.DashBoard .container{
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;

    margin: 50px 70px 2px 70px;
    
}
.DashBoard .container .student-manage ,
.view-result,
.Create-exam {
    border: 2px outset rgb(179, 173, 173);
    padding: 10px;
    height: 160px;
    width: 120px;
    margin: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 2px 0 14px rgba(233, 21, 21, 0.945); 
    border-radius: 14px;
    font-family: 'Belanosima', sans-serif;
    text-align: center;
    text-transform: capitalize;
    font-size: 20px;
    color: lightslategray;
    
}
.DashBoard .container .student-manage:hover ,
.view-result:hover,
.Create-exam:hover {
    border: 2px solid rgb(236, 67, 67);
    padding: 7px;
    color: #f13333;
    box-shadow: 0 0 14px rgba(248, 2, 2, 0.945); 
   
}
.DashBoard .container .student-manage img ,
.view-result img,
.Create-exam img {

    mix-blend-mode: lighten;
    height: 125px;
    width: 100px;
}



@media  screen and (max-width:1024px) {
    .DashBoard .dash-header .dash-welcome{
        font-size: 20px;
       
    }
    .DashBoard .dash-header .nav_icon{
        font-size: 15px;
        margin: 3px;
    }
    .dash-header .nav_icon img{
        width: 30px;
        margin-bottom: -5px;
    }
    .DashBoard .container .student-manage ,
.view-result,
.Create-exam {
    width: 120px;
    margin: 20px; 
}
}

@media  screen and (max-width:600px) {
  .DashBoard{
    width: 120vw;
  }
  .DashBoard .container{


    margin: 40px 0px 2px 40px;
    
}
    
    
}


