@import url('https://fonts.googleapis.com/css2?family=Lilita+One&family=Rajdhani:wght@300&display=swap');

.adminlogin{
    width: 100%;
    height: 100vh;
    position: relative;
    
}
.adminbox{
    width: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 30px;
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    
}
.adminbox img{
    position: relative;
    left: 40%;
    user-select: none;
    height: 60px;
    width: 50px;
}
.adminbox h1{
    margin-bottom: 30px;
    text-align: center;
    color: white;
    text-transform: uppercase;
    font-family: 'Lilita One', cursive;
    letter-spacing: 2px;
    user-select: none;
    
}
.adminbox .inputBox{
    position: relative;
   
    font-family: 'Rajdhani', sans-serif;
}
.adminbox .inputBox input{
 width: 100%;
 padding: 10px;
 font-size: 16px;
 color: white;
 letter-spacing: 1px;
 margin-bottom: 30px;
 border: none;
 border-bottom: 1px solid white;
 background: transparent;
 outline: none;

}
.adminbox .inputBox label{
    position: absolute;
    top: 0;
    left: 0;
    letter-spacing: 1px;
    padding: 10px 0;
    font-size: 16px;
    color: white;
    transition: 0.5s;
    user-select: none;
    z-index: -99;
}


.adminbox .inputBox input:focus ~ label,
.adminbox .inputBox input:valid ~ label{
    top: -20px;
    left: 0;
    color: red;
    font-size: 12px;

}
.adminbox input[type = 'submit']{
    font-family: 'Rajdhani', sans-serif;
    font-weight: 900; 
    letter-spacing: 4px;
    position: relative;
    background: transparent;
    border: none;
    outline: none;
    padding: 8px 16px;
    color: white;
    border-radius: 5px;
    background: #e4110a;
    font-size: 18px;
    left: 70%;

}
.backHome{
    color: rgb(42, 43, 41);
    font-family: 'Rajdhani', sans-serif; 
    user-select: none;
}
.backHome .link{
   text-decoration: none;
   color:red;
   font-weight: 700;
}

.adminbox .hideShow{
    position: absolute;
    left: 80%;
    bottom: 45%;
}
 .adminbox .hideShow img{
    height: 25px;
    width: 20px;
  
}

@media  screen and (max-width:1024px) {
    .adminbox{
        width: 300px;
        top: 35%;
        left: 50%;
    }
    .adminbox img{
        height: 50px;
        width: 40px;
        margin: 0 auto;
        
    }
    .adminbox h1{
        font-size: 20px;
    }
}

@media  screen and (max-width:600px) {
   .adminbox{
    left: 185px;
    top: 245px;
   }
  
    
}